import { useState } from 'react';
import './App.css';
import AnupDaju from './Components/AnupDaju/AnupDaju';
import Calendar from './Components/Calendar';

function App() {

  const [toggle, setToggle] = useState(false);

  const toogleCalendar = () => {
    setToggle(!toggle);
  }

  return (
    <div className="App">
      <div
        onClick={toogleCalendar}
        style={{ position: 'absolute', backgroundColor: toggle ? 'red' : 'green', width: '20xp', height: '20px', right: '10px', borderRadius: '50px' }}>

      </div>

      {
        toggle ? <Calendar /> : <AnupDaju />
      }
    </div>
  );
}

export default App;
